"use client";

import * as React from "react";
import Head from "next/head";
import { Loader2 } from "lucide-react";
import axiosClient from "@/lib/axiosClient";
import Preview from "@/app/preview/dummuypreview/page";
import { useParams } from "next/navigation";

export default function LandingPageClient() {
  const params = useParams();
  const slug = Number(params.slug);

  const [isLoading, setIsLoading] = React.useState(true);
  const [payload, setPayload] = React.useState<any>(null);
  const [metaData, setMetaData] = React.useState<{
    meta_title?: string;
    meta_description?: string;
    keywords?: string;
  }>({});

  React.useEffect(() => {
    if (!slug || isNaN(slug)) {
      setIsLoading(false);
      return;
    }

    const fetchData = async () => {
      try {
        setIsLoading(true);

        // Fetch both APIs in parallel
        const [landingRes, productRes] = await Promise.all([
          axiosClient.get(`/products/${slug}/landing-page`),
          axiosClient.get(`/products/${slug}`),
        ]);

        // Handle meta data from product details
        if (productRes.data?.success && productRes.data?.data) {
          const product = productRes.data.data;
          setMetaData({
            meta_title: product.meta_title || product.name || "Product Preview",
            meta_description: product.meta_description || "Preview of the product landing page.",
            keywords: product.keywords || "",
          });
        }

        // Handle landing page sections
        if (landingRes.data?.success && landingRes.data?.data?.sections?.length) {
          const activeSections = landingRes.data.data.sections
            .filter((sec: any) => sec.is_published)
            .sort((a: any, b: any) => a.sort_order - b.sort_order);

          setPayload({
            templateName: "modern",
            sections: activeSections.map((sec: any) => ({
              section: sec.global_section.key,
              content: sec.content ?? {},
            })),
            styles: {
              primaryColor: "#ecedf4ff",
              backgroundColor: "#000000",
              textColor: "#a97d38",
              headlineSize: 28,
              paragraphSize: 16,
            },
          });
        } else {
          setPayload({
            templateName: "modern",
            sections: [
              {
                section: "hero",
                content: {
                  title: "No Sections Published",
                  subtitle: "Enable sections in the builder to see them here.",
                },
              },
            ],
            styles: {
              primaryColor: "#ecedf4ff",
              backgroundColor: "#000000",
              textColor: "#a97d38",
              headlineSize: 28,
              paragraphSize: 16,
            },
          });
        }
      } catch (error) {
        console.error("Error fetching data:", error);

        // Fallback meta
        setMetaData({
          meta_title: "Product Preview",
          meta_description: "Error loading product details.",
        });

        // Fallback payload
        setPayload({
          templateName: "modern",
          sections: [
            {
              section: "hero",
              content: {
                title: "Error Loading Preview",
                subtitle: "Please try again later.",
              },
            },
          ],
          styles: {
            primaryColor: "#ecedf4ff",
            backgroundColor: "#000000",
            textColor: "#a97d38",
            headlineSize: 28,
            paragraphSize: 16,
          },
        });
      } finally {
        setIsLoading(false);
      }
    };

    fetchData();
  }, [slug]);

  if (isLoading) {
    return (
      <div className="flex h-screen items-center justify-center">
        <Loader2 className="h-10 w-10 animate-spin" />
      </div>
    );
  }

  if (!payload) {
    return <div className="p-8 text-center">No preview data available.</div>;
  }

  return (
    <>
      <Head>
        <title>{metaData.meta_title || "Product Preview"}</title>
        {metaData.meta_description && (
          <meta name="description" content={metaData.meta_description} />
        )}
        {metaData.keywords && (
          <meta name="keywords" content={metaData.keywords} />
        )}
        {/* Optional: Open Graph / Social Meta Tags */}
        <meta property="og:title" content={metaData.meta_title} />
        {metaData.meta_description && (
          <meta property="og:description" content={metaData.meta_description} />
        )}
      </Head>

      <Preview data={payload} productId={slug} />
    </>
  );
}